import React, { useEffect, useState, useRef } from "react";
import { Link, useParams } from "react-router-dom";
import Headeradmin from "../../components/Admin/HeaderAdmin";
import NavAdmin from "../../components/Admin/NavAdmin";
import { Modal, Button } from "react-bootstrap";
import DataTable from "react-data-table-component";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTrash,
  faArrowLeft,
  faCalendarAlt,
  faUserPlus,
  faComment,
  faStar,
  faPenToSquare,
} from "@fortawesome/free-solid-svg-icons";
import { BsCloudUpload } from "react-icons/bs";
import axios from "axios";
import moment from "moment";
import Notification from "../../components/Notification";
import AlertDelete from "../../components/Admin/AlertDelete";
import { useNavigate } from "react-router-dom";

export default function SendNotification() {
  const { slug } = useParams();
  const navigate = useNavigate();
  const [refreshNotifications, setRefreshNotifications] = useState(true);
  const [currentRecord, setCurrentRecord] = useState({
    name: "",
    id: "",
    start_date: "",
    end_date: "",
    time: "",
    location: "",
    description: "",
    image: null,
  });
  const [messageset, setmessageset] = useState("");
  const [alertpopup, setalertpopup] = useState(false);
  const [makeImagePrivate, setMakeImagePrivate] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [selectedFile, setSelectedFile] = useState(null);
  const [deleteid, setdeleteid] = useState("");
  const [records, setRecords] = useState([]);
  const [UserDetail, setUserDetail] = useState("");
  const [Joinevents, setJoinevents] = useState([]);
  const [showModalJoin, setShowModalJoin] = useState("");
  const [showModalInterested, setShowModalInterested] = useState("");
  const [Interestedevents, setInterestedevents] = useState([]);
  const [showModalInvite, setShowModalInvite] = useState(false);
  const [Inviteevents, setInviteevents] = useState([]);
  const [showModalEdit, setShowModalEdit] = useState(false);
  const [messageupdate, setmessageupdate] = useState(false);
  const [spinner, setspinner] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [sendmessage, setsendmessage] = useState(false);
  const apiUrl = "https://backend.amourette.no/api/adminsendnotification/";
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("adminLogin");
    const userLogin = JSON.parse(storedUsername);
    console.log(userLogin);
    if (userLogin === null) {
      navigate("/admin/login");
    }
  }, []);
  useEffect(() => {
    const ws = new WebSocket("wss://backend.amourette.no");

    ws.onmessage = (event) => {
      try {
        const receivedMessage = JSON.parse(event.data);
        // console.log(receivedMessage);
      } catch (error) {
        console.error("Failed to parse WebSocket message:", error);
        console.error("Raw message:", event.data);
      }
    };

    ws.onopen = () => {
      console.log("WebSocket connection established");
    };

    ws.onerror = (error) => {
      console.error("WebSocket error:", error);
    };

    return () => {
      ws.close();
      console.log("WebSocket connection closed");
    };
  }, []);
  useEffect(() => {
    getnotification();
  }, []);
  const getnotification = async () => {
    let formData = {
      user_id: "",
    };
    console.log(formData);
    try {
      const res = await axios.post(apiUrl + "getnotificationadmin", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setRecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Create a Date object using the provided date string
    const postDate = new Date(dateString);

    // Create an instance of Intl.DateTimeFormat for the "Europe/Oslo" timezone
    const options = {
      timeZone: "Europe/Oslo",
      year: "numeric",
      month: "numeric",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false,
    };

    // Get the date and time in the specified timezone
    const localDate = new Intl.DateTimeFormat("en-US", options).format(
      postDate
    );

    // Get the current date in the same timezone
    const now = new Date();
    const localNow = new Intl.DateTimeFormat("en-US", options).format(now);

    // Parse the formatted dates back into Date objects
    const postDateLocal = new Date(localDate);
    const nowLocal = new Date(localNow);

    // Calculate the difference in seconds
    const secondsDiff = Math.floor((nowLocal - postDateLocal) / 1000);

    // Define time intervals
    const intervals = [
      { label: "year", seconds: 31536000 }, // 365 days
      { label: "month", seconds: 2592000 }, // 30 days
      { label: "day", seconds: 86400 }, // 1 day
      { label: "hour", seconds: 3600 }, // 1 hour
      { label: "minute", seconds: 60 }, // 1 minute
      { label: "second", seconds: 1 }, // 1 second
    ];

    for (const interval of intervals) {
      const count = Math.floor(secondsDiff / interval.seconds);
      if (count >= 1) {
        return `${count} ${interval.label}${count > 1 ? "s" : ""} ago`;
      }
    }

    return "Just now";
  };
  const columns = [
    {
      name: "Message",
      selector: (row) => row.message,
      sortable: true,
    },

    {
      name: "Time",
      selector: (row) => formateTime(row.date),
      sortable: true,
      className: "age",
    },

    {
      name: "Action",
      cell: (row) => (
        <div className="d-flex align-items-center gap-3">
          <a
            onClick={() => handledelete(row.id)}
            href="javascript:void(0)"
            className="dataedit_btn text-danger"
            title="Delete"
          >
            <FontAwesomeIcon icon={faTrash} />
          </a>
        </div>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];
  const [searchQuery, setSearchQuery] = useState("");

  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  const handledelete = async (id) => {
    setdeleteid(id);
    setalertpopup(true);
    setmessageset("Are you sure? If you want to delete this");
  };
  const deletenotification = async () => {
    let formData = {
      id: deleteid,
    };
    try {
      const res = await axios.post(apiUrl + "deletenotifications", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;

      getnotification();
      setalertpopup(false);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    var message = e.target.message.value;
    let formData = {
      message: message,
    };
    console.log(formData);
    try {
      const res = await axios.post(apiUrl + "sendallusers", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      setsendmessage(true);
      getnotification();
      setTimeout(() => {
        getnotification();
        setShowModal(false);
        setsendmessage(false);
      }, 1500);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const handlesend = () => {
    setShowModal(true);
  };
  const handleModalCloseJoin = () => {
    setShowModal(false);
  };
  //Location
  const [isOpen, setIsOpen] = useState(false);

  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };

  return (
    <>
      <div class="position-relative bg-white d-flex p-0">
        <Headeradmin isOpen={isOpen} />
        <div className={`content ${isOpen ? "open" : ""}`}>
          <NavAdmin onClick={toggleNavAdmin} />
          {alertpopup && (
            <AlertDelete
              isOpen1={alertpopup}
              onClose1={() => setalertpopup(false)}
              message={messageset}
              onReturnSent={deletenotification}
            />
          )}
          <div className="container-fluid pt-4 px-4">
            <div className="row g-4">
              <form action="javascript:void(0)" method="post">
                <div className="col-12">
                  <div className="bg-light rounded h-100 p-4">
                    <div className="d-flex justify-content-between">
                      <a
                        href="/admin/users"
                        style={{
                          display: "flex",
                          alignItems: "center",
                          textDecoration: "none",
                        }}
                        className="mb-4"
                      ></a>
                      <button
                        className="mb-4 btn btn-primary"
                        type="button"
                        onClick={() => handlesend()}
                      >
                        Send Notification
                      </button>
                    </div>

                    <div className="d-flex justify-content-between">
                      <div className="d-flex justify-content-between">
                        <h6 className="mb-4">SendNotification List</h6>
                      </div>
                      <div className="mb-3">
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Search..."
                          value={searchQuery}
                          onChange={(e) => setSearchQuery(e.target.value)}
                        />
                      </div>
                    </div>

                    {/* Start of row */}
                    <div className="table-responsive">
                      <DataTable
                        columns={columns}
                        data={filteredRecords}
                        pagination
                        highlightOnHover
                        noDataComponent="No records found."
                      />
                    </div>
                    {/* End of row */}
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <Modal
        show={showModal}
        className="edit_admin"
        onHide={handleModalCloseJoin}
      >
        <Modal.Header closeButton>
          <Modal.Title className="textcolor">
            Send Notification to All Users - Announcements
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {sendmessage && (
            <span className="text-success mb-4">Successfully sent</span>
          )}
          <form
            onSubmit={handleSubmit}
            action="javascript:void(0)"
            method="post"
          >
            <div className="mb-3">
              <label htmlFor="name" className="form-label">
                Name
              </label>
              <input
                required
                type="text"
                className="form-control"
                placeholder="Enter here..."
                id="name"
                name="message"
              />
            </div>

            <Modal.Footer>
              <Button variant="secondary" onClick={handleModalCloseJoin}>
                Close
              </Button>

              <Button
                type="submit"
                variant="primary"
                className="bg-success border-color-none"
              >
                Send
              </Button>
            </Modal.Footer>
          </form>
        </Modal.Body>
      </Modal>
    </>
  );
}
